div.home-outer {
  width: 100%;
  background-color: #1DA57A;
  height: 100vh;
  align-items: center;
  display: flex;
  justify-content: center;
  overflow: hidden;
  flex-direction: column;
}
div.home-outer div.home {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  box-shadow: 0 0 0.5em #000000;
  background-color: #fff;
}
div.home-outer div.home div.title {
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1em;
}
div.home-outer div.home div.title div.title-main {
  width: 100%;
}
div.home-outer div.home div.title img {
  margin: 0 0.5em;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}
@media (max-width: 400px) {
  div.home-outer div.home div.title img {
    display: none;
  }
}
@media (max-width: 635px) {
  div.home-outer div.home div.title {
    width: 100%;
  }
}
div.home-outer div.home div.main {
  width: 100%;
  display: flex;
  flex-direction: row;
}
div.home-outer div.home div.main div.nav {
  width: 70%;
  height: 100%;
  padding: 1em;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
div.home-outer div.home div.main div.nav ::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
div.home-outer div.home div.main div.nav ::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border-radius: 1em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  background: #1DA57A;
}
div.home-outer div.home div.main div.nav div.content {
  flex-direction: column;
  position: relative;
  max-height: calc(100vh - 100px);
  min-height: calc(100vh - 100px);
  overflow: hidden;
  border-radius: 0.5em;
  display: flex;
  box-shadow: 0 0 0.5em #1DA57A;
  background-color: #fff;
}
div.home-outer div.home div.main div.tips {
  width: 30%;
  border-left: 1px solid #dedede;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 300px;
}
div.home-outer div.home div.main div.tips div.tips-inner {
  width: 100%;
  height: 100%;
  padding: 0.5em;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
div.home-outer div.home div.main div.tips div.tips-inner div.todo-item {
  display: flex;
  cursor: pointer;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  margin: 0.5em 0;
  border: 1px solid rgba(172, 172, 172, 0.479);
}
div.home-outer div.home div.main div.tips div.tips-inner div.todo-item div.todo-item-inner {
  padding: 0.25em;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: hidden;
}
div.home-outer div.home div.main div.tips div.tips-inner div.todo-item div.todo-item-inner span {
  width: 100%;
  height: 100%;
}
div.home-outer div.home div.main div.tips div.tips-inner div.add-content {
  border: 0;
}
div.home-outer div.home div.main div.tips div.tips-inner div.add-content input {
  border: 1px solid #1DA57A;
  outline: none;
  width: 60%;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  text-indent: 0.5rem;
}
div.home-outer div.home div.main div.tips div.tips-inner div.add-content button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border: 1px solid #1DA57A;
  width: 60%;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  background-color: #fff;
}
@media (max-width: 635px) {
  div.home-outer div.home div.main div.nav {
    width: 100%;
    background-color: #1DA57A;
  }
  div.home-outer div.home div.main div.nav div.content {
    box-shadow: 0 0 0.5em #000000;
  }
  div.home-outer div.home div.main div.tips {
    display: none;
  }
}
