div.search {
  display: flex;
  position: absolute;
  flex-direction: column;
  right: 7px;
  width: 40px;
  height: 80px;
  bottom: 90px;
}
div.search button {
  width: 100%;
  height: 50%;
}
div.content-outer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
div.content-outer ::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
div.content-outer ::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border-radius: 1em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  background: #1DA57A;
}
div.content-outer div.detail-content {
  border-radius: 0.5em;
  width: 100%;
  padding: 0.5em;
  background-color: #fff;
  overflow: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  list-style: circle;
}
div.content-outer div.detail-content h1 {
  border-bottom: 1px solid #ccc;
}
div.content-outer div.detail-content pre {
  padding: 0.5em;
  padding-top: 0;
  margin: 0;
  font-size: 0.8em;
  border-top: 0.2em solid #c3deb7;
  border-bottom: 0.2em solid #c3deb7;
  border-left: 0.2em solid #c3deb7;
  border-right: 0.2em solid #c3deb7;
  width: 100%;
  background-color: #f6f8fa;
}
div.content-outer div.detail-content p {
  margin: 0;
}
div.content-outer div.detail-content code {
  background-color: #f6f8fa;
  cursor: pointer;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  margin: 0 2px;
}
div.content-outer div.detail-content code:hover {
  background-color: #c3deb7;
}
