div.form {
  overflow: auto;
}
div.form div.content-item {
  flex-direction: column;
  display: flex;
  padding: 1em 0.5em;
}
div.form div.content-item a.title {
  text-decoration: underline;
  font-size: 1.1em;
}
div.form div.content-item div.content-item-text span.text-main {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-right: 0.5em;
}
div.form div.content-item div.content-item-text span.text-main span.text-time {
  margin-right: 0.5em;
  font-size: 0.8em;
}
